import axios from 'axios';
import React, { useState } from 'react'

export default function Movies() {
let [loadicon,setloadicon]=useState(false);
let [thetargetmovie,setmovie]=useState("");
let[word,setword]=useState("");
function ownset(event){
  let theword=event.target.value;
theword=theword.toLowerCase();
setword(theword);
}


let x=0;

      async  function searchit(){
        let newarrofmovies=[]
        setloadicon(true)     
let theword=word;

 
let arrofmovies=[];
setmovie("");
//if(event.key=="Backspace"){
  //return 0;
//}

for (let index = 1; index < 500; index++) {
  if(x==1){break;}
  let {data}=await axios.get(`https://api.themoviedb.org/3/movie/popular?page=${index}&api_key=eba8b9a7199efdcb0ca1f96879b83c44&fbclid=IwAR2nGZbXyhVaykqJkEoUqs7qEmMXkl5XU-sgq19_QtrZcWLyKtuNZFN7oh8`);
  
  for (let index = 0; index < data.results.length; index++) {
if(x==1){break;}
  if(data.results[index].original_title.toLowerCase().includes(theword)){
            if(theword.includes(data.results[index].original_title.toLowerCase())){
                 
              arrofmovies.unshift(data.results[index])
             
              
              x=1;
              if(x==1){break;}
            }else{
              if(x==1){break;}
              arrofmovies.push(data.results[index])
              
              
            }

  }
    
  }
}

for (let index = 0; index <arrofmovies.length; index++) {
  if(arrofmovies[index].original_title.toLowerCase().includes(theword)){
    if(theword.includes(arrofmovies[index].original_title)){
     newarrofmovies.unshift(arrofmovies[index]);
    }else{
      newarrofmovies.push(arrofmovies[index]);
    }

  }
  
}

setmovie( newarrofmovies);

arrofmovies=[];
 newarrofmovies=[]
 setloadicon(false)
}


  return (
    <>
    <h1>if you want film like<span className='text-danger'>(batman)</span> you can write bat or batman and we will find it</h1>
    <h2>key words can help you [godfather , spider-man, day , thor , avenger]</h2>
    <div className='text-center my-5'>
      <h3>search for any film to get it`s global vote</h3>
      <h2 className='text-center text-warning'>N0TE:THE FIRST SEARCH TAKE FROM 10 : 59 SECONDS</h2>
      <input onKeyUp={ownset} placeholder="Api SEARCH PLEASE ENTER KEY-WORD"  type="text" className='searchengine'/>
      <button onClick={searchit} className="buttonsearch">search {loadicon?<i class="fa-solid fa-spinner fa-spin-pulse fa-spin-reverse"></i>:""}</button>
    </div>
              <div className="container">
                <div className="row">
                {thetargetmovie!="" ? thetargetmovie.map((obj,index)=>{

return <div key={index} className="col-md-4 ACNCH">
<div className='position-relative' >
  <img className='w-100 rounded' src={"https://image.tmdb.org/t/p/w500"+obj.backdrop_path} alt="" />
{obj.original_title}
<span className='position-absolute bg-danger p-1 top-0 start-0 thevote'>VOTE: <br/>{obj.vote_average}</span>
</div>
</div>}): ""}

                </div>
              </div>


    

 

    </>
  )
}
