import React from 'react';
import { useState } from 'react';
import Joi from 'joi';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

export default function Login(props) {
    let tohome=useNavigate();
    let [joierror,puterror]=useState("");
    let [thedataofuser,putthedata]=useState({
    
   
email:"",   
password:"",      

    });
    function changesinform(e){
        let newobj={...thedataofuser}
       let thevalue=e.target.value;
       newobj[e.target.name]=thevalue;
       putthedata(newobj)
    }

async function nowyoucansed(e){

    e.preventDefault();
    let checker=checkwithregular();
    if(checker?.error){
        let joimessage=``;
        console.log(checker.error.details);
for (let index = 0; index < checker.error.details.length; index++) {
    if(checker.error.details[index].message.includes("first_name")){
        joimessage+=`the first name must not have number and must be more than 3 `; 
    }else if(checker.error.details[index].message.includes("last_name")){
        joimessage+=`the last name must not have number and must be more than 3  `;  
    }else if(checker.error.details[index].message.includes("password")){
        joimessage+=`the  pass must more than 3 char  `;
    }else{
        joimessage+=checker.error.details[index].message ;
    }
    
}
puterror(joimessage);


}else{
let {data}=await axios.post(" https://route-egypt-api.herokuapp.com/signin ",thedataofuser);

if(data?.message=="success"){
   
    localStorage.setItem("thetoken",data.token)
    props.navechanges();
    tohome("/home");
}else{
    puterror(data.message);
   
}

}
    
}
function checkwithregular(){
let thebase=Joi.object({
    first_name:Joi.string().pattern(new RegExp(/^[a-zA-Z ]{3,24}$/)),    
    last_name:Joi.string().pattern(new RegExp(/^[a-zA-Z ]{3,24}$/)),    
    email:Joi.string().email({tlds:{allow: ["com","net","org","gov"]}}),   
    password:Joi.string().pattern(new RegExp(/^[a-zA-Z0-9]{3,24}$/)),      
     age:Joi.number().integer().min(1).max(100)

});
return thebase.validate(thedataofuser,{abortEarly:false})
}

  return (
    <>
     <h1 className='text-center'>WELCOME TO BABEL SITE  <svg width="45" height="45" viewBox="0 0 48 48" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M20 14.5V3.99997L28 3.99999V13.5" stroke="#333" stroke-width="1" stroke-linejoin="round"/><path d="M14 25V15.4619L34 13V23" stroke="#333" stroke-width="1" stroke-linejoin="round"/><path d="M11 35V26L37 23V32" stroke="#333" stroke-width="1" stroke-linejoin="round"/><path d="M40 44H8V36L40 32V44Z" fill="none" stroke="#333" stroke-width="1" stroke-linejoin="round"/></svg>   </h1>
    <div className='w-75 m-auto p-5 containerofinput text-center'>
        <form onSubmit={(e)=>{return nowyoucansed(e)}}>

        <input  onChange={(e)=>{return changesinform(e)}}  className='form-control' type="text" name="email"  placeholder='please enter your e-mail'/>
       
       
        <input  onChange={(e)=>{return changesinform(e)}}  className='form-control' type="password" name="password"  placeholder='please enter your password'/>
<button type='submit'  className='buttonofsgin'>Login Now</button>
<br/>
<span className='text-danger'>{joierror!=""?joierror:""}</span>
</form>
    </div>
    </>
  )
}
