import React, { useEffect, useState } from 'react'
import jwt_decode from 'jwt-decode'
import axios from 'axios';
import '@fortawesome/fontawesome-free';
import { useNavigate } from 'react-router-dom';
export default function Home() {
  let gotootherpage=useNavigate();
  let thenameofuser=localStorage.getItem("thetoken");
  thenameofuser=jwt_decode(thenameofuser);
  thenameofuser=thenameofuser.first_name;
let [tv,settv]=useState(null);

  async function gettrends(){
let {data}=await axios.get(`https://api.themoviedb.org/3/trending/tv/day?api_key=eba8b9a7199efdcb0ca1f96879b83c44&fbclid=IwAR2nGZbXyhVaykqJkEoUqs7qEmMXkl5XU-sgq19_QtrZcWLyKtuNZFN7oh8`);
     settv(data.results)
   
  }
 useEffect(()=>{
    gettrends();
 
  },[]);
 
  function letusmove(id,type){
    localStorage.setItem("theid",id);
    localStorage.setItem("thetype",type);
    gotootherpage("/show");
    
  }
  return (<>
   <marquee scrollamount="25" loop="true" width="100%" direction="right" height="auto">
   <h2 >REMEMBER {thenameofuser} BABEL SITE WATCH YOU<i className="fa-solid fa-eye  fa-fade"></i> </h2>
</marquee>
   
<div className="container text-center">
  <div className="row">
  <div className="col-md-8">
      <div>
      <h2 className='text-danger'>welcom in OUR tv/show TRENDS</h2>
     <h2 className='fs-100 text-danger'><i className="fa-brands fa-react fa-beat"></i></h2>
      </div>
    </div>
  {tv&&tv.map((val,index)=>{return <>
    <div key={index} className="col-md-4 ACNCH">
      <div onClick={()=>{letusmove(val.id,"tv")}}>
        <img className='w-100 rounded' src={"https://image.tmdb.org/t/p/w500"+val.backdrop_path} alt="" />
      {val.name}
      
      </div>
    </div>
  
  
  </>})}
    
  </div>
</div>



</>
  )
}
