import React from 'react'
import { Link } from 'react-router-dom'
import {useNavigate} from 'react-router-dom'
export default function Navv(props) {
 let toregis=useNavigate();
  function tosginout(){

    localStorage.clear();
    props.changeconroller(0);
    toregis("/register");

  }
  return (
    <>
<nav className="navbar navbar-expand-lg  navbar-light">
  <div className="container-fluid">
    < a  className="navbar-brand" ><i className="fa-brands fa-reacteurope fs-1 fa-shake"></i></ a >
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>


    <div className="collapse navbar-collapse" id="navbarNav">
      <ul className="navbar-nav">
{props.thecontroller==0?"":<><li className="nav-item">
          < Link to="/home"  aria-current="page" >TVhome</ Link >
        </li>
        <li className="nav-item">
          < Link  to="/movies"  >Movies_search</ Link >
        </li>
        <li className="nav-item">
          < a onClick={tosginout}>SGINOUT</ a >
        </li>
        </>}
        
{props.thecontroller==0?<> <li className="nav-item">
        < Link  to="/register"  >REGISTER</ Link >
        </li>
        <li className="nav-item">
        < Link  to="/login"  >SIGNIN </ Link >
        </li></>:""}
       
        
      </ul>
    </div>
  </div>
</nav>
    
    </>
  )
}
