import axios from 'axios';
import React, { useEffect, useState } from 'react'
import jwt_decode from 'jwt-decode'; 
export default function Show() {
    let theid=localStorage.getItem("theid");
    let thetype=localStorage.getItem("thetype");
   let token=localStorage.getItem("thetoken");
    let thetoken=localStorage.getItem("thetoken");
     thetoken=jwt_decode(thetoken);
     
   let[crossdata,setcrossdata]= useState(null);
 let[commint,setcommint]=useState({title:"",desc:"",USERID:thetoken._id,token});
  let[thetitle,setthetitle]=useState("");
  let [arrnotes,setarrnotes]=useState(null);
 async function getdata(){
let {data}=await axios.get(`https://api.themoviedb.org/3/${thetype}/${theid}?api_key=eba8b9a7199efdcb0ca1f96879b83c44&fbclid=IwAR2nGZbXyhVaykqJkEoUqs7qEmMXkl5XU-sgq19_QtrZcWLyKtuNZFN7oh8&language=en-US`)

setcrossdata(data);

setcommint({...commint,title:data.name})

thetitle=data.name;
setthetitle(data.name)

    }
function objofcommint(e){

 setcommint({...commint, "desc": e.target.value+`(  ADDED BY ${thetoken.first_name} )`  })


}
async function sendcommint(e){
e.preventDefault();
 let {data}=await axios.post(`https://route-egypt-api.herokuapp.com/addNote`,commint);

getthenotes();
}
async function getthenotes(){
 let thearrofnotes=[];
  
let {data}=await axios.get(`https://route-egypt-api.herokuapp.com/getUserNotes`,{

headers:{
  "token":commint.token,
  
}

})

for(let i=0;i<data.Notes.length;i++){

  if(data.Notes[i].title==thetitle){
    
    thearrofnotes.unshift(data.Notes[i]);

  }
  
}

setarrnotes(thearrofnotes)
}
    useEffect(()=>{
        
            getdata()
            
            getthenotes()
          

    },[]);
  return (
    
    <>
    <div className='container'>
        <div className='row my-5'>
            {crossdata&&
            
<>
<div className="col-md-6">
            <img className='w-100 shadow-lg p-1 mb-5 bg-dark rounded' src={"https://image.tmdb.org/t/p/w500"+crossdata?.backdrop_path} alt=""  />
            
            </div>
            <div className="col-md-6">
                <h2>the overview</h2>
                <p>{crossdata?.overview}</p>
           <span className='text-danger'>WATCH IT:</span>   <a target="_blank"  href={crossdata?.homepage}><button className='btnowatch'>GO TO {crossdata?.networks[0]?.name}</button></a>  
            </div>
            <div className="col-md-6">
<form onSubmit={sendcommint}><textarea name="desc" onChange={objofcommint} className='w-100 bg-transparent' placeholder='please add your commint'  id="" cols="30" rows="10"></textarea>
              <button  className='w-100 bord bg-primary'>ADD YOUR COMMINT IN THE FILM</button></form>
            
            </div>

            <div className="col-md-6">

              <button onClick={getthenotes} className='w-100 bg-danger text-white bord'>SHOW (USERS) COMMINTS</button>
            <div>
              
              {arrnotes&&arrnotes.map((y,i)=>{
                  return <div className='border-under' key={i}>{y.desc}</div>
                                   
              })}
              
            </div>
            </div>
              
</>
            }
           
        </div>
    </div>
    
    </>
  )
}
